@font-face {
    font-family: "Ubuntu";
    src: url("./assets/fonts/Ubuntu-Bold.ttf") format("truetype");
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: "Ubuntu";
    src: url("./assets/fonts/Ubuntu-Medium.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: "Ubuntu";
    src: url("./assets/fonts/Ubuntu-Regular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
}

// Variables SCSS

$main-font-family: "Ubuntu", sans-serif;
$sub-text: 12px;
$sm-text: 14px;
$md-text: 16px;
$xl-text: 32px;
//
$media-sm-text: 14px;

$media-xl-text: 24px;
$sm-weight: 400;
$md-weight: 500;
$xl-weight: 700;

// Colors
$main-bg: #eff5ff;
$accent-dark: #022959;
$accent-white: #ffffff;
$accent-sky: #abc0ff;
$accent-blue: #483eff;
$accent-grey: #9699aa;
$accent-light-sky: #bee2fd;
$accent-orange: #ffaf7e;
$accent-pink: #f9818e;
$accent-red: #ee374a;
$input-border: #d6d9e6;
$hover-btn: #164a8a;
$card-bg: #f8f9ff;

.container {
    max-width: 940px;
    min-height: 600px;
    width: 100%;
    padding: 16px;
    border-radius: 15px;
    background-color: $accent-white;
}

.d-flex {
    display: flex;
    gap: 100px;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeInForm {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.universalForm {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    animation: fadeInForm 1s ease-in;
    .infoAboutUniversalStep {
        display: flex;
        flex-direction: column;
        gap: 11px;
        margin-bottom: 35px;
        h2 {
            font-size: $xl-text;
            font-weight: $xl-weight;
            line-height: 37px;
        }
        p {
            font-size: $md-text;
            line-height: 25px;
            color: $accent-grey;
        }
    }
}

@media (max-width: 830px) {
    .d-flex {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0;
    }
    .container {
        background-color: transparent;
        min-height: auto;
        padding: 32px 16px 32px 16px;
    }
    .universalForm {
        .infoAboutUniversalStep {
            margin-bottom: 22px;
            h2 {
                font-size: $media-xl-text;
                font-weight: $xl-weight;
                line-height: 28px;
            }
        }
    }
}
